      catslider: [],
      url:'https://bizlx.itechvision.in',
      // url:'http://127.0.0.1:8000',
    }
  },
  methods: {
    getSlider() {
      axios.get('home/catsliders')
          .then((resp) => {
            console.log(resp);
            this.catslider = resp.data.catsliders;
          });
    },
  },
  mounted() {
    this.getSlider();
  },
  // computed: {
