<template>
  <div>
    <div class="mobile">
      <carousel v-if="ads.length > 0" :autoplay="false" :margin="30" :items="4" :dots="false" :slideBy="1"
              :navText="['‹','›']"
              :responsive = "{0:{items:1},576:{items:2},768:{items:3},1200:{items:4}}"
              :loop="true">
      <div v-for="ad in ads" :key="ad.id" class="cole">
        <router-link :to="{name: 'businessprofile', params: {id: ad.business_id, uname:ad.business_uname}}">
          <div class="overlay"></div>
            <img :src=(url+ad.ad_img_url) height="160" :alt=ad.business_name>
            <p class="adtitle">
              {{ad.business_name}}
            </p>
        </router-link>
      </div>
    </carousel>
    </div>
    <div class="desktop">
      <carousel v-if="ads.length > 0" :autoplay="false" :margin="30" :items="4" :dots="false" :slideBy="1"
              :navText="['‹','›']"
              :responsive = "{0:{items:1},576:{items:2},768:{items:3},1200:{items:4}}"
              :loop="true">
      <div v-for="ad in ads" :key="ad.id" class="cole">
        <router-link :to="{name: 'businessprofile', params: {id: ad.business_id, uname:ad.business_uname}}">
          <div class="overlay"></div>
            <img :src=(url+ad.ad_img_url) class="img-fluid top-listing" :alt=ad.business_name>
            <p class="adtitle">
              {{ad.business_name}}
            </p>
        </router-link>
      </div>
    </carousel>
    </div>
  </div>
</template>

<script>
import carousel from 'vue-owl-carousel';
import axios from "axios";
export default {
  name: "HomeAds",
  components:{carousel},
  data (){
    return{
      url:'https://bizlx.itechvision.in',
      // url:'http://127.0.0.1:8000',
      ads:[],
    }
  },
  created() {
    this.allTopads();
  },
  methods:{
    allTopads(){
      axios.get('home/topads')
          .then((resp)=>{
            this.ads = resp.data.topads;
          });
    }
  }
}
</script>

<style scoped>
.overlay {
  background: #00000054;
  position: absolute;
  height: 100%;
  width: 100%;
}
img.img-fluid.top-listing {
  width: 100%;
  height: 250px;
  object-fit: cover;
}
 p.adtitle {
  position: absolute;
  z-index: 999;
  bottom: 10px;
  font-weight: bold;
  font-size: 20px;
  margin: 0;
  width: 100%;
  color: #fff;
  text-align: center;
}
.mobile p.adtitle {
  position: absolute;
  z-index: 999;
  bottom: 10px;
  font-weight: bold;
  font-size: 20px;
  margin: 0;
  width: 100%;
  color: #fff;
  text-align: start;
  margin-left: 15px;
}
</style>