<template>
  <div>

    <v-form @submit.prevent="ClickSearch" class="catsearch d-flex mobile bg-white">
      <v-autocomplete dense hide-details placeholder="Category"
          v-model="selectSubCategoty"
          :items="categories"
          item-value="sub_cat_id"
          item-text="subcat_name"
          id="sel1" class="sel1"></v-autocomplete>
      <v-autocomplete dense hide-details placeholder="Location"
          v-model="selectCity"
          :items="allCities" 
          item-value="city_id"
          item-text="city"
          id="sel1" class="sel1"></v-autocomplete>
      <v-autocomplete dense hide-details placeholder="Deals" 
          v-model="deal"
          :items="deals" id="sel1" class="sel1"></v-autocomplete>
      <v-btn type="submit" max-width="36px" min-width="36px" color="cred"><v-icon class="text-white col-1">mdi-magnify</v-icon></v-btn>
    </v-form>
    <v-form @submit.prevent="ClickSearch" class="catsearch d-flex desktop">
        <v-autocomplete dense rounded hide-details placeholder="Category" 
            v-model="selectSubCategoty"
            :items="categories"
            item-value="sub_cat_id"
            item-text="subcat_name"
            id="sel2" class="sel2" prepend-inner-icon="mdi-format-list-bulleted-square"></v-autocomplete>
        <v-autocomplete dense rounded hide-details placeholder="Location" 
            v-model="selectCity"
            :items="allCities" 
            item-value="city_id"
            item-text="city"
            id="sel2" class="sel2" prepend-inner-icon="mdi-map-marker"></v-autocomplete>
        <v-autocomplete dense rounded hide-details placeholder="Deals" 
            v-model="deal"
            :items="deals" id="sel2" class="sel2" prepend-inner-icon="mdi-checkbox-marked-outline"></v-autocomplete>
        <v-btn type="submit" color="cred"><v-icon class="text-white">mdi-magnify</v-icon>Search</v-btn>
    </v-form>
    

    <v-snackbar v-model="snackbar" :timeout="snackbar.timeout" class="snackbar-shadow">{{ text }}
        <template v-slot:action="{ attrs }">
            <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">OK</v-btn>
        </template>
    </v-snackbar>


  </div>
</template>

<script>

import axios from 'axios';
import { EventBus } from "@/event-bus";
export default {
  name: "CatSearch",
  
  data: () => ({
      selectSubCategoty: '',
      selectCity: '',
      deal: '',
      categories: [],
      locations:[],
      allCities: [],
      deals:['Hot Deals', 'Sales','Jobs', 'Videos'],

      snackbar: '',
      text: '',
      timeout: 3000,

      data1: []
  }),
  mounted(){
    this.getSearch();
  },
  methods: {
      getSearch() { // all cat get
          axios.get("catsearch")
              .then((resp) => {
                  this.categories = resp.data.subcategories;
                  this.allCities = resp.data.cities;
              }
          );
      },
      
      // selectCategory(){ // Get all city according to cat
      //     this.locations = [];
      //     for(var i = 0; i <= this.allCities.length; i++){
      //         if(this.allCities[i].sub_cat_id==this.selectSubCategoty){
      //           this.locations.push(this.allCities[i]);
      //         }
      //     }
      // },
      
      ClickSearch(){
          if(this.deal==''){
                this.snackbar = true; 
                this.text = "Please select deal for search.";
          }else{
          const config = {
              headers: { 'content-type': 'multipart/form-data' }
          }
            let data = { 
                selectSubCategoty: this.selectSubCategoty,
                selectCity: this.selectCity,
                deal: this.deal,
            };
            axios.post("search/category", data, config)
                .then((resp) => {
                  localStorage.removeItem('catSearchedValues');
                  if(resp.data.dealsType=="hot deals"){ // deals page
                      if(resp.data.allhotDeals.length>0){
                        setTimeout(()=>{
                            EventBus.$emit('event_bus_emitted', { // payload with data
                              allhotDeals:resp.data.allhotDeals,
                            })
                        },1000);
                        this.$router.push({name: 'alldeals'});
                        localStorage.setItem("catSearchedValues", JSON.stringify(data));
                      }else{
                        this.snackbar = true; 
                        this.text = "Hot deals not found.";
                      }
                  }else if(resp.data.dealsType=="jobs"){ // jobs page 
                    if(resp.data.allJobs.length>0){
                      setTimeout(()=>{
                          EventBus.$emit('event_bus_emitted', { // payload with data
                            allJobs:resp.data.allJobs,
                          })
                      },1000);
                      this.$router.push("/jobs/search/page");
                      localStorage.setItem("catSearchedValues", JSON.stringify(data));
                    }else{
                      this.snackbar = true; 
                      this.text = "Jobs not found.";
                    }
                  }else if(resp.data.dealsType=="videos"){ // videos page
                    if(resp.data.allVideos.length>0){
                      setTimeout(()=>{
                          EventBus.$emit('event_bus_emitted', { // payload with data
                            allVideos:resp.data.allVideos,
                          })
                      },1000);
                      this.$router.push("/allvideos");
                      localStorage.setItem("catSearchedValues", JSON.stringify(data));
                    }else{
                      this.snackbar = true; 
                      this.text = "Videos not found.";
                    }
                  }else if(resp.data.dealsType=="sales"){ // videos page
                    if(resp.data.allSales.length>0){ 
                      setTimeout(()=>{
                          EventBus.$emit('event_bus_emitted', { // payload with data
                            allSales:resp.data.allSales,
                          })
                      },1000);
                      this.$router.push("/sales/all");
                      localStorage.setItem("catSearchedValues", JSON.stringify(data));

                    }else{
                      this.snackbar = true; 
                      this.text = "Videos not found.";
                    }
                  }
                },
            );
          }
      }

  }
}
</script>

<style scoped>
.catsearch.d-flex {
  border: 1px solid;
  border-radius: 4px;
  padding: 0 0 0 4px;
}
.sel2 {
  font-size: 14px;
}
#sel1::placeholder {
  color: #000000 !important;
  opacity: 1;
}
.v-btn {border-radius: 0 4px 4px 0;}
@media(min-width: 576.1px){
  .mobile{display: none !important;}
}
@media(max-width: 576px){
  .desktop{display: none !important;}
  .sel1 {
    font-size: 13px;
  }

}

</style>