<script>
import AdminBar from './views/admin/layout/AdminBar.vue';
import FooterView from "@/views/FooterView";
import axios from "axios";
export default {
  name: "App",
  components: {FooterView, AdminBar },
  metaInfo: {
    title: 'Bizlx.com™ Search Daily Local Hot Deals, Sales, Latest Arrivals, Videos of Shopping Stores Around You',
    titleTemplate: '%s | Bizlx.com™',
    htmlAttrs: {
      lang: 'en',
      amp: true
    },
    meta: [
      { charset: 'utf-8' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1' },
    ],
  },
  data(){
      return{
        user:null,
      }
  },
  async created() {
      const response = await axios.get('user');
      this.$store.dispatch('user',response.data);
  },
  methods: {
    scrollTop(){
      document.getElementById('app').scrollIntoView();
    },
  }
};
</script>
