<template>
  <div>
    <v-form action="/jobs/by-city" class="jobsearch d-flex mobile bg-white">
      <v-autocomplete dense hide-details placeholder="Job Type"
          v-model="selectJobType"
          @change="selectedJobType"
          :items="jobstypes" 
          item-value="id"
          item-text="job_type"
        id="sel1" class="sel1"></v-autocomplete>
      <v-autocomplete dense hide-details placeholder="City"
          v-model="selectCity"
          :rules="nameRules"
          :items="locations" 
          item-value="city_id"
          item-text="city"
          id="sel1" class="sel1"></v-autocomplete>
      <v-autocomplete dense hide-details placeholder="Experience"
          v-model="selectExperience"
          :items="catExperience"
          id="sel1" class="sel1"></v-autocomplete>
      <v-btn type="submit" max-width="36px" min-width="36px" color="cred"><v-icon class="text-white col-1">mdi-magnify</v-icon></v-btn>
    </v-form>
    <v-form @submit.prevent="ClickSearchJob" class="jobsearch d-flex desktop">
      <v-autocomplete dense hide-details placeholder="Job Type"
          v-model="selectJobType"
          @change="selectedJobType"
          :items="jobstypes" 
          item-value="id"
          item-text="job_type"
          id="sel2" class="sel2 spacd"  prepend-inner-icon="mdi-format-list-bulleted-square"></v-autocomplete>
      <v-autocomplete dense hide-details placeholder="City"
          v-model="selectCity"
          :rules="nameRules"
          :items="locations" 
          item-value="city_id"
          item-text="city"
          id="sel2" class="sel2 spacd" prepend-inner-icon="mdi-map-marker"></v-autocomplete>
      <v-autocomplete dense hide-details placeholder="Experience"
          v-model="selectExperience"
          :items="catExperience"
          id="sel2" class="sel2 spacd" prepend-inner-icon="mdi-checkbox-marked-outline"></v-autocomplete>
      <v-btn type="submit" color="cred"><v-icon class="text-white ">mdi-magnify</v-icon>Search</v-btn>
    </v-form>

    <v-snackbar v-model="snackbar" :timeout="snackbar.timeout" class="snackbar-shadow">{{ text }}
        <template v-slot:action="{ attrs }">
            <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">OK</v-btn>
        </template>
    </v-snackbar>

  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: "JobSearch",
    data: () => ({
      selectJobType: '',
      selectCity: '',
      selectExperience: '',
      jobstypes: [],
      locations: [],
      allCities: [],
      catExperience: ['Fresher', '6 Month - 1 Year', '1 Year - 2 Year'],

      snackbar: '',
      text: '',
      timeout: 3000,

    }),
    mounted(){
      this.jobTypes();
    },

    methods: {
      jobTypes() { // all job types get
          axios.get("jobsearch")
              .then((resp) => {
                  this.jobstypes = resp.data.jobtypes;
                  this.allCities = resp.data.cities;
              }
          );
      },
      selectedJobType(){ // Get all city according to job type
          this.locations = [];
          for(var i = 0; i <= this.allCities.length; i++){
              if(this.allCities[i].job_type_id==this.selectJobType){
                this.locations.push(this.allCities[i]);
              }  
          }
      },
      ClickSearchJob() {

            const config = {
                headers: { 'content-type': 'multipart/form-data' }
            }
            let data = { 
              selectJobType: this.selectJobType,
              selectCity: this.selectCity,
              selectExperience: this.selectExperience
            };
            axios.post("search/jobs/category", data, config)
                .then((resp) => {

                  console.warn(resp.data);
                    // router.push({name : 'maincategories', 
                    // params: {
                    //     snackbar: true, 
                    //     text: resp.data.message,
                    //     timeout: 2000,
                    // }});
                }
            );
          this.$router.push("/jobs/search/page");
        // }
      }
    }
    
}
</script>

<style scoped>
.jobsearch.d-flex {
  border: 1px solid;
  border-radius: 4px;
  padding: 0 0 0 4px;
  }
.sel2 {
  font-size: 14px;
  }
 .spacd {
  padding: 0 24px !important;
  }
#sel1::placeholder {
  color: #000000 !important;
  opacity: 1;
  }
.v-btn {border-radius: 0 4px 4px 0;}
@media(min-width: 576.1px){
  .mobile{display: none !important;}
  }
@media(max-width: 576px){
  .desktop{display: none !important;}
  .sel1 {
    font-size: 13px;
    }

  }
</style>